import { Fragment } from "react";
import {styled, Box} from '@mui/material';

//component didmount(react life cycle) used in cls based component but we will use use effect for functional component..use effect (react hook)is used for rendering of all data when page opens
import { useEffect } from "react";

//components
import NavBar from "./NavBar";
import Banner from "./Banner";
import Slide from './Slide';
import MidSlide from "./MidSlide";
import MidSection from "./MidSection";

import { getProducts } from "../../redux/actions/productActions";
import {useDispatch, useSelector} from 'react-redux';

const Component = styled(Box)`
    padding:10px;
    background: #F2F2F2;
`

const Home = () => {

  const {products} = useSelector(state => state.getProducts);

  // console.log(products);

  //object destructing concept
  //const {products} = getProducts;
  //or we can use
  //getProducts.products;

  const dispatch = useDispatch();

  useEffect(()=> {
    dispatch(getProducts());
  },[dispatch])

  return (
    <Fragment>
      <NavBar />
      <Component>
      <Banner />
      <MidSlide products={products} title="Deal of the Day" timer={true}/>
      <MidSection/>
      <Slide products={products} title="Discounts for You" timer={false}/>
      <Slide products={products} title="Suggested Items" timer={false}/>
      <Slide products={products} title="Top Selections" timer={false}/>
      <Slide products={products} title="Recommended Items" timer={false}/>
      <Slide products={products} title="Trending Offers" timer={false}/>
      <Slide products={products} title="Season's Top Picks" timer={false}/>
      <Slide products={products} title="Top Deals on Accessories" timer={false}/>
      </Component>
    </Fragment>
  );
};
export default Home;
